import React from 'react';
import styled from '@emotion/styled';
import { Link } from "gatsby";
import slug from "slug";

import { css } from "@emotion/core";


export default (props) => {
const Containerqc2tp = styled.div`
background-color:#bbb;
padding:15px 0 0 40px;
`
const Contentqc2tp = styled.div`
color:#222;
`

  const { edges } = props;
  let tags = [];
  let categories = [];

  edges.forEach(({ node }) => {
    tags = Array.from(new Set([...tags, ...node.frontmatter.tags]));
    categories = Array.from(
      new Set([...categories, ...node.frontmatter.categories])
    );
  });

  return (
        <Containerqc2tp>
         <Contentqc2tp>
					      
					        <h3>Thẻ</h3>
					        {
					          tags.map((tag, index) => (
					            <span key={index}>
					              <Link to={`/${slug(tag).toLowerCase()}/`} css={css`font-size:15px;color:#333;text-decoration: none;display: list-item;line-height:1.4rem;`}>
					                {tag}
					              </Link>{" "}
					            </span>
					          ))
					        }
					      

         </Contentqc2tp>
        </Containerqc2tp>
  )
}