import React from 'react'
import { Global, css } from "@emotion/core"
import styled from "@emotion/styled"
import Header from '../components/header'
import Qc1 from '../components/qc1'
import Sidebar from '../components/sidebar'
import { StaticQuery, graphql } from "gatsby";


export default props => {
 

const Containertp = styled.div`
`
const Bodytp = styled.div`
display: flex;
  display: -webkit-flex;
  margin:0 auto;
max-width:1000px;
background-color: #bbb;
`
const Childrentp = styled.div`
width: calc(100% - 280px);
background-color: #ccc;
`
const Sidebatp = styled.div`
width:280px;
`
  return (
    <StaticQuery
      query={graphql`
      query {
        site {
          siteMetadata {
            title
          }
        },
        topics: allMarkdownRemark(sort: {order: DESC, fields: [frontmatter___date] }) {
          edges {
            node {
              frontmatter {
                categories
                tags
              }
            }
          }
        }
      }
    `}
      render={data => (



        <Containertp>
            <Global
                  styles={css`
                      body {
                          margin:0;
                          background: #222;
                          font-family:arial;
                      }

                      h1{
                          margin-top:0;
                      }
                  `}
            />


            <Header />
           
            <Bodytp>
            <Childrentp> {props.children}</Childrentp>
            <Sidebatp><Sidebar edges={data.topics.edges}/></Sidebatp>
           
            </Bodytp>
        </Containertp>


        
   )}
    />
  )
}
