import React from "react";
import { Link } from "gatsby";
import styled from '@emotion/styled';
import { css } from "@emotion/core"

const Headertp = styled.div`
background-color: #222;
padding-top:40px;
`
const Contenttp = styled.div`
margin:0 auto;
max-width:1000px;
text-align: center;
padding-bottom:10px;
`

const Ultp = styled.ul`
display: inline-flex;
list-style-type:none;
padding: 0;
font-size: .9rem;
font-weight: 500;
`
const Litp = styled.li`
padding-right: 15px;
padding-left: 15px;
`
const Header = () => {
    return(
        <Headertp>
        <Contenttp>
            <h1><Link to='/' css={css`color:#fff;text-decoration: none;`}>
                Trang Bị Vip
                </Link></h1>
            <nav>
                <Ultp> 
                <Litp><Link to="./game/" css={css`color:#fff;text-decoration: none;`}>Game (Trò Chơi)</Link></Litp>
                   <Litp><Link to="./manga/" css={css`color:#fff;text-decoration: none;`}>Manga (Truyện Tranh)</Link></Litp>
                </Ultp>
            </nav>
       </Contenttp>
       </Headertp> 
    )
} 

export default Header